.wrapper {
  display: grid;
  grid-template-columns: 102px 213px;
  grid-template-rows: 19px;
  align-items: center;
  padding-bottom: 13px;
}
.wrapper__lable-select {
  color: var(--regent-gray);
  height: 19px;
}
.custom-select__container {
  height: 19px !important;
}
.custom-select__control {
  border: none !important;
  outline: none !important;
  max-width: 150px !important;
  max-height: 19px !important;
  margin-left: 20px !important;
  box-shadow: none !important;
  justify-content: flex-start !important;
  background-color: transparent !important;
}
.custom-select__value-container {
  padding: 0 !important;
  height: 19px !important;
}
.custom-select__input-container {
  padding: 0 !important;
  margin: 0 !important;
  flex: 0 !important;
  grid-template-rows: 1/2 !important;
  height: 19px !important;
}
.custom-select__indicator {
  cursor: pointer;
}

.custom-select__menu-list {
  
  padding: 0 !important;
  max-height: 140px !important;
  overflow-y: auto !important;
  scrollbar-color: #c4c4c4 #eaeaea !important;
  scrollbar-width: 7px !important;

}
.custom-select__menu-list::-webkit-scrollbar{
  width: 7px !important;
  background:#eaeaea !important;
}
.custom-select__menu-list::-webkit-scrollbar-track{
  background:#eaeaea;
}
.custom-select__menu-list::-webkit-scrollbar-thumb{
  border-radius: 8px;
  background: #c4c4c4;
  width: 7px;
}
.custom-select__control--menu-is-open {
  border: none !important;
  outline: none !important;
  background-color: transparent !important;
}
.custom-select__control--is-focused {
  border: none !important;
  outline: none !important;
}
.custom-select__indicator-separator {
  display: none !important;
}

.custom-select__option {
  cursor: pointer !important;
}
.custom-select__option--is-focused {
  background-color: var(--wild-sand) !important;
}
.custom-select__option--is-selected {
  background-color: var(--wild-sand) !important;
  color: var(--outer-space) !important;
}
.custom-select__option--is-selected::before {
  content: url("/src/accets/images/IconCheck.svg");
  padding-right: 6px;
}

.errorLabel {
  color: #f37575;
}

.error {
  color: #f37575;
  grid-column: 2/3;
  margin-left: 20px;
}

@media screen and (min-width: 768px) {
  .custom-select__menu {
    width: 325px !important;
  }
}